import{copyToClipboard}from'../../common';export default{name:'VanDocContent',data(){return{iframeDocument:null};},computed:{currentPage(){const{path}=this.$route;if(path){return path.split('/').slice(-1)[0];}return this.$route.name;}},watch:{$route(to,from){if(from.path!==to.path){setTimeout(()=>{this.copyAction();});}}},mounted(){this.copyAction();},methods:{onClick({target}){if(['H2','H3','H4','H5'].includes(target.tagName)){this.scrollToAnchor(target);}},scrollToAnchor(target){if(target.id){this.$router.push({name:this.$route.name,hash:'#'+target.id});this.syncMobilePos(target.id);}},syncMobilePos(id){// Getting the document at this point is to ensure that the target has been fully rendered.
if(!this.iframeDocument){const iframe=document.querySelector('iframe');this.iframeDocument=iframe.contentWindow.document;}if(this.iframeDocument){const target=this.iframeDocument.getElementById(id);if(target){target.scrollIntoView(true);}}},copyAction(){const codeBoxes=document.querySelectorAll('.van-doc-card pre code');if(!codeBoxes||!codeBoxes.length){return;}for(let i=0;i<codeBoxes.length;i++){const item=codeBoxes[i];let timer=null;item.addEventListener('click',()=>{if(timer)return;const content=item.innerText;copyToClipboard(content);item.classList.add('code-copy-success');timer=setTimeout(()=>{item.classList.remove('code-copy-success');timer=null;},1400);});}}}};