export default{name:'VanDocSimulator',props:{src:String},data(){return{scrollTop:window.scrollY,windowHeight:window.innerHeight};},computed:{isFixed(){return this.scrollTop>60;},simulatorStyle(){const height=Math.min(640,this.windowHeight-90);return{height:height+'px'};}},mounted(){window.addEventListener('scroll',()=>{this.scrollTop=window.scrollY;});window.addEventListener('resize',()=>{this.windowHeight=window.innerHeight;});}};