},data(){return{showSource:false,copyStatus:'ready'};},methods:{unescape,toggleSource(){this.showSource=!this.showSource;},copySourceCode(){copyToClipboard(unescape(this.originCode));this.copyStatus='copied';setTimeout(()=>{this.copyStatus='ready';},2000);}}};