import{packageVersion}from'site-desktop-shared';import{getDefaultTheme,syncThemeToChild}from'../../common/iframe-sync';export default{name:'VanDocHeader',props:{lang:String,config:Object,versions:Array,langConfigs:Array,darkModeClass:String},data(){return{currentTheme:getDefaultTheme(),packageVersion,showVersionPop:false};},computed:{langLink(){return`#${this.$route.path.replace(this.lang,this.anotherLang.lang)}`;},langLabel(){return this.anotherLang.label;},anotherLang(){const items=this.langConfigs.filter(item=>item.lang!==this.lang);if(items.length){return items[0];}return{};},themeImg(){if(this.currentTheme==='light'){return'https://b.yzcdn.cn/vant/dark-theme.svg';}return'https://b.yzcdn.cn/vant/light-theme.svg';}},watch:{currentTheme:{handler(newVal,oldVal){window.localStorage.setItem('vantTheme',newVal);document.documentElement.classList.remove(`van-doc-theme-${oldVal}`);document.documentElement.classList.add(`van-doc-theme-${newVal}`);syncThemeToChild(newVal);},immediate:true}},methods:{toggleTheme(){this.currentTheme=this.currentTheme==='light'?'dark':'light';},toggleVersionPop(){const val=!this.showVersionPop;const action=val?'add':'remove';document.body[`${action}EventListener`]('click',this.checkHideVersionPop);this.showVersionPop=val;},checkHideVersionPop(event){if(!this.$refs.version.contains(event.target)){this.showVersionPop=false;}},onSwitchLang(lang){this.$router.push(this.$route.path.replace(lang.from,lang.to));},onSwitchVersion(version){if(version.link){location.href=version.link;}}}};