import DocNav from'./Nav.vue';import DocHeader from'./Header.vue';import DocContent from'./Content.vue';import DocContainer from'./Container.vue';import DocSimulator from'./Simulator.vue';export default{name:'VanDoc',components:{DocNav,DocHeader,DocContent,DocContainer,DocSimulator},props:{lang:String,versions:Array,simulator:String,langConfigs:Array,hasSimulator:Boolean,darkModeClass:String,config:{type:Object,required:true},base:{type:String,default:''}},emits:['switch-version'],watch:{$route(){this.setNav();}},created(){this.setNav();this.keyboardHandler();},methods:{setNav(){const{nav}=this.config;const items=nav.reduce((list,item)=>list.concat(item.items),[]);const currentPath=this.$route.path.split('/').pop();let currentIndex;for(let i=0,len=items.length;i<len;i++){if(items[i].path===currentPath){currentIndex=i;break;}}this.leftNav=items[currentIndex-1];this.rightNav=items[currentIndex+1];},keyboardNav(direction){if(/win(32|64)/.test(navigator.userAgent.toLocaleLowerCase())){return;}const nav=direction==='prev'?this.leftNav:this.rightNav;if(nav.path){this.$router.push(this.base+nav.path);}},keyboardHandler(){window.addEventListener('keyup',event=>{switch(event.keyCode){case 37:// left
this.keyboardNav('prev');break;case 39:// right
this.keyboardNav('next');break;}});}}};